@import url("https://fonts.googleapis.com/css2?family=Victor+Mono&display=swap");

html,
body {
  margin: 0;

  padding: 0;
}

* {
  box-sizing: border-box;
  font-family: "Victor Mono", monospace;
}

.navbar {
  background-color: #121212;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  height: 50px;
}

#root {
  margin: 0;
  padding: 0;
}

.img-box {
  padding-left: 20px;
  height: 100%;
}

img {
  height: 100%;
}

ul {
}

ul,
li {
  display: inline-block;
  color: white;
  font-weight: bold;
}
li {
  margin: 2px 10px;
  text-transform: uppercase;
}

.split {
  display: flex;
  justify-content: center;

  gap: 10px;
  width: 100%;
  padding: 20px;
  margin-top: 10px;
}

.box1 {
  background-color: #f1c40f;
  /* order: 2; */
  min-height: 500px;
  padding: 20px;
  width: 100%;
  border-radius: 10px;
}

.box2 {
  background-color: #f1c40f;
  width: 100%;
  min-height: 500px;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.box3 {
  background-color: #f1c40f;
  width: 100%;
  min-height: 500px;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item {
  display: flex;
  gap: 10px;
  flex-direction: row;
  align-items: center;
  background-color: #121212;
  padding: 10px;
  border-radius: 5px;
  color: white;
  cursor: pointer;
  margin-bottom: 10px;
}

.item:hover {
  background-color: #34495e;
}

.item img {
  width: 60px;
  height: auto;
}

.item-contents {
  align-self: flex-start;
}

.item-contents h1 {
  padding: 0;
  margin: 0;
  font-size: 20px;
}

.item-contents2 h1 {
  padding: 0;
  margin: 0;
  font-size: 18px;
  line-height: 2em;
}

.item-contents p {
  margin: 5px 0px;
  padding: 0;
  font-size: 14px;
  color: #f1c40f;
}

.input {
  padding: 10px;
  width: 80%;
  border-radius: 5px;
  border: none;
  color: #121212;
}
.btn {
  margin-top: 10px;
  padding: 10px 30px;
  border: none;
  border-radius: 5px;
  background-color: #121212;
  color: white;
  animation: anim 1s ease-in infinite;
  cursor: pointer;
}

.fav {
  display: flex;
}

h1 {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.box2 h1 span,
.box3 h1 span,
.player span {
  background-color: black;
  width: 45px;
  height: 45px;

  display: flex;
  justify-content: center;
  align-items: flex-start;
  border-radius: 50%;
  color: white;
  cursor: pointer;
  padding-top: 7px;
}

.img-box2 {
  display: flex;
  gap: 10px;

  flex-direction: row;
}

.b2 h2 {
  margin: 0;
  padding: 0;
}
.error {
  text-align: center;
  background-color: black;
  /* color: #f1c40f; */
  padding: 20px;
  border-radius: 10px;
  animation: anim 1s ease-in infinite;
}

.loading {
  font-size: 20px;
  font-weight: bold;
  display: flex;
  align-items: center;
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  justify-content: center;
  background-color: black;
  color: #f1c40f;
  animation: anim 1s ease-in infinite;
}

@keyframes anim {
  0% {
    color: red;
  }
  50% {
    color: greenyellow;
  }
  100% {
    color: #f1c40f;
  }
}

@keyframes anim2 {
  0% {
    outline: #c0392b solid 3px;
  }
  50% {
    outline: #8e44ad solid 3px;
  }
  100% {
    outline: #3498db solid 3px;
  }
}

.input:focus {
  border: none;
  animation: anim2 1s ease-in infinite;
}

code {
  font-size: 12px;
  font-style: italic;
  margin-top: 10px;
  color: orangered;
  font-weight: bold;
}

.full-box {
  min-height: 300px;
}

@media (max-width: 680px) {
  .back h1 {
    font-size: 14px !important;
    text-align: center;
  }

  .navbar {
    display: flex;
    flex-direction: column;
    height: auto;
  }

  .navbar .img-box {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 40px;
  }

  .navbar ul {
    display: block;
    width: 100%;
    padding: 0;
    margin: 6px 0px 0px 0px;
    text-align: center;
    display: flex;
    justify-content: center;
    background-color: #121212;
  }

  .navbar ul li a {
    color: #f1c40f;
    padding: 0;
    margin: 0;
  }

  .navbar ul li a.active {
    color: white;
  }

  .box2 h1 {
    font-size: 20px;
    text-align: center;
  }

  i {
    font-size: 12px !important;
    line-height: 2em;
    text-align: center;
  }

  .about h1 {
    font-size: 16px;
  }
}

@media (max-width: 825px) {
  .split {
    flex-direction: column;
  }
  .box1 {
    order: 2;
  }

  .box3 h1 {
    font-size: 20px;
    text-align: center;
  }

  .b1 img {
    width: 100% !important;
  }

  .img-box2 {
    flex-direction: column;
  }
  .btn {
    width: 100% !important;
  }

  i {
    text-align: center;
  }

  code {
    text-align: center;
  }

  .box2 {
    min-height: 73vh;
    justify-content: center;
  }

  .box3 {
    height: 80vh;
    align-items: center;
  }
}

.footer {
  background-color: #121212;
  /* position: relative;
  bottom: 0; */
  width: 100%;
  color: white;
  display: flex;
  justify-content: center;
}

.container {
  margin-bottom: 10px;
  min-height: 100%;
}

i {
  font-size: 16px;
  margin-top: 30px;
  font-weight: bold;
  margin-bottom: 5px;
  animation: anim 0.5s ease-in infinite;
  padding: 10px;
  border-radius: 10px;
  background: black;
  text-align: center;
  line-height: 2em;
}

ul li {
  cursor: pointer;
}

.navbar img {
  cursor: pointer;
}
.all {
  display: flex;
  flex-direction: column;
}

.main-p {
  margin: 5px 0px 25px 0px;
  text-align: center;
}

.page404 {
  display: flex;
  justify-content: center;
  animation: anim 1s ease-in infinite;
  background-color: #121212;
  height: 100vh;
  align-items: center;
}

a {
  color: white;
  text-decoration: none;
}

a.active {
  color: #f1c40f;
}

.selectedMovie {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.selectedMovie h1 {
  font-size: 20px;
  text-align: center;
}
.selectedMovie img {
  width: 150px;
}

.links {
  line-height: 3em;
  display: flex;

  flex-direction: column;
  align-items: center;
}

.links a {
  font-size: 18px;
  text-align: center;
  background-color: #121212;
  padding: 10px;
  margin: 5px;
  border-radius: 10px;
  border-radius: 10px;
  width: 100%;
}

.links a:hover {
  color: #f1c40f;

  padding: 10px;

  animation: anim 1s ease-in infinite;
}

.player {
  margin-top: 10px;
  margin-bottom: 10px;
}

.back {
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.back h1 {
  font-size: 20px;
}

.about {
  padding: 10px;
  display: flex;
  justify-content: center;
  height: 100vh;
  align-items: center;
  text-align: center;
}

.about h1 {
  background-color: #121212;
  padding: 10px;
  border-radius: 10px;
  line-height: 2em;

  animation: anim 1s ease-in infinite;
}

.linktitle {
  position: relative;
  width: 100%;
  top: 5px;
}
